import { useCallback } from 'react';
import { useRouteMatch, useLocation } from 'react-router-dom';

import { resolvePathname } from '@mssgme/helpers';

/**
 * @param {{ useMatchUrl: boolean }} [defaults] Generator default options
 * @returns {{
 *   pathname: string;
 *   state: *;
 *   generateUrl: (path: string, options?: { useMatchUrl: boolean }) => string;
 * }} Generator method
 */
export function useNavigationUtils(defaults) {
    const { url } = useRouteMatch();
    const { pathname, state } = useLocation();

    const generateUrl = useCallback(
        (path, options) => {
            const isRelative = path.charAt(0) !== '/';

            if (isRelative) {
                options = defaults ? { ...defaults, ...options } : options;

                return resolvePathname(options?.useMatchUrl ? url : pathname, path);
            }

            return resolvePathname(path);
        },
        [url, pathname, defaults?.useMatchUrl]
    );

    return { generateUrl, pathname, url, state };
}
