import { ACTION_CREATE, ACTION_EDIT } from '../../request';
import { Voter } from '../voter';

export class ProductsVoter extends Voter {
    supports(request) {
        return request.role === 'basic' && request.resource === 'products';
    }

    vote(request) {
        if (request.includes(ACTION_CREATE)) {
            if (!this.canCreate(request)) {
                return false;
            }
        }

        if (request.includes(ACTION_EDIT)) {
            if (!this.canEdit(request)) {
                return false;
            }
        }

        return true;
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canCreate(request) {
        return this.canEdit(request);
    }

    canEdit({ subject, workspace }) {
        if (subject?.slug) {
            return true;
        }

        return workspace.card.products.count < 10;
    }
}
